@use "sass:math";

.noselect {
 -webkit-touch-callout: none;
 /* iOS Safari */
 -webkit-user-select: none;
 /* Safari */
 -khtml-user-select: none;
 /* Konqueror HTML */
 -moz-user-select: none;
 /* Old versions of Firefox */
 -ms-user-select: none;
 /* Internet Explorer/Edge */
 user-select: none;
 /* Non-prefixed version, currently
 supported by Chrome, Edge, Opera and Firefox */
}

::-moz-selection {
 background: $tint-color;
 color: $base-background-color;
}

::selection {
 background: $tint-color;
 color: $base-background-color;
}

html {
 min-height: 100vh;
 font-size: $base-font-size;
}

body {
 background: $base-background-color;
 font-family: $base-font-family;
 font-weight: $base-font-weight;
 line-height: $base-line-height;
 color: $base-color;

 display: flex;
 flex-direction: column;
 min-height: 100vh;
}

p {
 margin-bottom: $spacing-unit;
}

h1,
h2,
h3,
h4,
h5 {
 margin: 1.38rem 0;
 font-weight: 600;
 line-height: 1.2;
}

h1 {
 margin-top: 0;
 font-size: 2.82rem;
}

h2 {
 font-size: 2.441rem;
}

h3 {
 font-size: 1.953rem;
}

h4 {
 font-size: 1.563rem;
}

h5 {
 font-size: 1.25rem;
}

small,
.text_small {
 font-size: 0.8rem;
}

ul {
 padding-left: $spacing-unit;
}


table {
 width: 100%;
 max-width: 100%;
 margin-bottom: 1rem;
}

table th {
 padding: 0.75rem;
 vertical-align: bottom;
 border-top: 1px solid #eceeef;
}

table td {
 padding: 0.75rem;
 vertical-align: top;
 border-bottom: 1px solid #eceeef;
}

table thead th {
 vertical-align: middle;
 border-bottom: 2px solid #eceeef;
}

/**
 * Links
 */
a {
 color: $base-color;

 &:visited {
 color: $base-color;
 }

 &:hover {
 color: $tint-color;
 text-decoration: underline;
 }

}

blockquote {
 border-left: math.div($spacing-unit, 4) solid $tint-color;
 padding: 0 $spacing-unit;
 margin: $spacing-unit 0;
 font-weight: 500;
 font-family: serif;
 font-size: 1.5rem;
}

blockquote cite {
 display: block;
 text-align: right;
}

.wrapper {
 margin: auto;
 max-width: $content-width;
}

.mClear {
 margin: 0;
 padding: 0;
}

header {
 background-color: $header-color;
}

.header {
 display: flex;
 flex-direction: column;
 padding: $spacing-unit 0;
}

.upper {
 display: flex;
 flex-direction: row;
 justify-content: space-between;
 align-items: center;
}

.left-nav {
 display: flex;
 align-items: center;
 text-decoration: none;

 &:hover {
 text-decoration: none;
 color: $base-color;
 }
}

.nav-root {
 font-family: $code-font-family;
 font-weight: 700;
}

.nav-cursor {
 width: 10px;
 height: 20px;
 background: $tint-color;
 margin-left: math.div($spacing-unit, 2);
 animation: blink 1s infinite;
}

.right-nav {
 display: flex;
 align-items: center;

 >* {
 margin-left: $spacing-unit;
 }

 :first-child {
 margin-left: 0;
 }
}

.social-links {
 display: flex;
 list-style-type: none;
 padding: 0;
 margin: 1.75 * $spacing-unit 0 0 0;
}

.social-link {
 margin-right: $spacing-unit;
}

.icon {
 fill: $base-color;
}

.align-center {
 display: flex;
 justify-content: center;
}

.image-box {
 display: flex;
 justify-content: center;
 flex-wrap: wrap;

 >* {
 margin: $spacing-unit;
 }
}

.full-width {
 width: 100%
}

.burger-nav {
 display: none;
}

.books {
 display: flex;
 max-width: 100%;
 flex-wrap: wrap;
 justify-content: space-between;
 flex-direction: row;
}

.book {
 max-width: 40%;
 min-width: 40%;
 margin-bottom: 2*$spacing-unit;
}

.book-cover {
 width: 120px;
 height: 100%;
 align-self: top;
 filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
 border-radius: 8px;
}

.book-detail-row {
 display: flex;
 flex-direction: row;
}

.book-text-col {
 margin-left: $spacing-unit;
 display: flex;
 flex-direction: column;

 a {
 display: flex;
 flex-direction: column;
 }
}

.book-title {
 font-weight: 500;
 text-overflow: ellipsis;
}

.book-subtitle {
 display: none;
 font-size: $small-font-size;
 font-weight: 500;
 color: $secondary-text-color;
}

.book-authors {
 font-size: $small-font-size;
 font-weight: 500;
 color: $secondary-text-color;
}

@media(prefers-reduced-motion:reduce) {
 .nav-cursor {
 animation: none
 }
}

@keyframes blink {
 0% {
 opacity: 0
 }

 50% {
 opacity: 1
 }

 100% {
 opacity: 0
 }
}

footer {
 display: flex;
 flex-direction: row;
 justify-content: center;
 align-items: center;
 bottom: 0px;
 margin-top: auto;
}

.post {
 margin-bottom: $spacing-unit;
 display: flex;
 align-items: flex-start;
 justify-content: space-between;
 border-bottom: 0.5px dashed $terciary-color;
 padding-bottom: $spacing-unit;
}

.post-meta {
 flex-shrink: 0;
 margin-top: -2px;
 color: $terciary-color;
 padding-left: $spacing-unit;
}

a.post-link {
 text-decoration: none;
}

a.post-link:hover {
 text-decoration: underline;
}

.page-title {
 margin: 1.5 * $spacing-unit 0;
}

.post-year {
 font-family: $code-font-family;
 font-weight: $base-font-weight;
 margin: 1.5 * $spacing-unit 0;
 color: $terciary-color;
}

nav ul {
 list-style-type: none;
 display: flex;
 padding: 0;
 margin: 0;

 >li {
 margin-left: $spacing-unit;
 }

 a {
 text-decoration: none;
 }
}

.post-tags {
 display: flex;
 margin-top: math.div($spacing-unit, 2);
}

.post-emoji {
 font-size: 3rem;
}

.post-tag {
 border: 1px solid $tint-color;
 color: $tint-color;
 margin-right: math.div($spacing-unit, 2);
 padding: math.div($spacing-unit, 8) 6px;
 border-radius: math.div($spacing-unit, 4);
 font-size: $base-font-size * 0.75;
}

@media screen and (max-width: 450px) {

 h1 {
 font-size: 1.802rem;
 }

 h2 {
 font-size: 1.602rem;
 }

 h3 {
 font-size: 1.424rem;
 }

 h4 {
 font-size: 1.266rem;
 }

 h5 {
 font-size: 1.125rem;
 }

 small,
 .text_small {
 font-size: 0.889rem;
 }

 .wrapper {
 margin: 0 $spacing-unit;
 }

 .burger-nav {
 display: block;
 cursor: pointer;
 }

 nav ul {
 display: none;
 background: $base-background-color;
 }

 nav ul {
 list-style-type: none;

 >li {
 margin-left: 0;
 }
 }

 nav ul.open {
 display: flex;
 align-items: flex-end;
 flex-direction: column;
 position: absolute;
 top: 3*$spacing-unit;
 right: $spacing-unit;
 padding: math.div($spacing-unit, 2) $spacing-unit;
 border-radius: math.div($spacing-unit, 4);
 border: 1px solid $terciary-color;
 }

 .book {
 min-width: 100%;
 max-width: 100%;
 }

 .book-text-col {
 display: flex;
 flex-direction: column;
 }

}

code.language-plaintext {
 background-color: #f4f4f5;
 /* Light gray background */
 color: #333333;
 /* Muted pink text for better readability */
 font-family: 'Courier New', Courier, monospace;
 /* Monospaced font for code */
 padding: 2px 5px;
 /* Small padding to make it stand out */
 border-radius: 4px;
 /* Rounded corners for smooth look */
 font-size: 0.95em;
 /* Slightly smaller font size to blend with text */
 border: 1px solid #e0e0e0;
}